	module.exports = {"data":{"markdownRemark":{"html":"<h3>Описание:</h3>\n<p>Одно из самых распространенных упражнений для тренировки трицепса. Но опасное. Точечная высокая нагрузка на локти может нанести травму. Удобно использовать штангу с EZ-грифом, но если её нету, то можно использовать с обычным грифом или гантели.</p>\n<h3>Порядок выполнения:</h3>\n<ol>\n  <li>Возьмите штангу хватом сверху немного уже уровня плеч. Лягте на горизонтальную скамью со штангой. Если используете гантели, то руки держите параллельно друг другу.</li>\n  <li>Исходное положение - выпрямите руки в локтях и немного отклоните их назад за голову (на 20 – 30 градусов).</li>\n  <li>На вдохе – удерживая плечи неподвижными, согните руки и поднесите штангу к голове. Угол в локтевом суставе быть 90°.</li>\n  <li>На выдохе - верните штангу в исходное положение.</li>\n  <li>После выполнения одной рукой, сделайте упражнение другой.</li>\n  <li>Сделайте упражнение нужное число раз.</li>\n</ol>\n<h3>Советы:</h3>\n<ul>\n  <li>Локти и плечи должны быть зафиксированы. Движение во время французского жима только в локтевом суставе. Подбирайте такой вес, при котором вы делаете упражнение правильно! Если вес мешает вам держать локти неподвижными – уменьшите его!</li>\n  <li>Старайтесь полностью выпрямляйте руки в локтях в верхней точке упражнения.</li>\n  <li>Оптимальная ширина хвата - 20 – 30 см. В таком положении трицепс загружен оптимально, и нагрузка на суставы равномерна.</li>\n  <li>Для французского жима необязательно нужна силовая скамья. Его вполне удобно делать прямо на полу!</li>\n  <li>Ни в коем случае не поднимайте штангу с пола из-за затылка уже после того, как легли на лавку. Или берите её в руки перед тем, как ложиться, или просите подать её Вам.</li>\n</ul>\n<h3>Видео-инструкция:</h3>\n<iframe width=\"100%\" height=\"550rem\" src=\"https://www.youtube.com/embed/SS5OO8ekqgI\" frameborder=\"0\" allow=\"autoplay; encrypted-media\" allowfullscreen></iframe>","frontmatter":{"path":"/exercise/francuzskii-jim-leja","efficiency":"6","title":"Французский жим лежа","image":"/img/4-03-0.jpg","image_2":"/4-03-1.png","image_3":"/4-03-2.pmg","image_4":"","filterby":"triceps","date":"April 12, 2017","injury":"7","bodypart":"Трицепс","filtertype":"isolated","type":"Изолир","filterinventory":"ezstange","inventory":"EZ-гриф","filterplace":null}}},"pathContext":{}}

